import React, { useEffect, useState } from "react";
import { List } from "reactstrap";
import AnimatedElement from "./AnimatedElement";
import { useDispatch, useSelector } from "react-redux";
import { getAcademyData } from "../reducers/academyReducer";

const PortfolioAcademySection = () => {
  const [academy, setAcademy] = useState(null);
  const { userdetails } = useSelector((state) => state.user);
  const dispatch = useDispatch();

  const getAcademy = async () => {
    const data = await dispatch(getAcademyData({ token: userdetails.token }));
    if (!data.error && data.payload !== {}) {
      await setAcademy(data.payload);
    } else {
      await setAcademy(null);
    }
  };

  useEffect(() => {
    getAcademy();
  }, [userdetails]);

  return (
    <>
      <List className="t-color f-size-20">
        {academy &&
          academy.map((item, i) => (
            <AnimatedElement key={item._id}>
              <li className="py-2">
                {item.academyName}
                <br />
                <span className="p-color f-size-15">
                  {item.instituteName}, {item.passingYear}
                </span>
              </li>
            </AnimatedElement>
          ))}
      </List>
    </>
  );
};

export default PortfolioAcademySection;
