import React, { useEffect, useState } from "react";
import { List } from "reactstrap";
import AnimatedElement from "./AnimatedElement";
import { useDispatch, useSelector } from "react-redux";
import { getEmployerData } from "../reducers/employerReducer";

const PortfolioEmployerSection = () => {
  const [employer, setEmployer] = useState(null);
  const { userdetails } = useSelector((state) => state.user);
  const dispatch = useDispatch();

  const getEmployer = async () => {
    const data = await dispatch(getEmployerData({ token: userdetails.token }));
    if (!data.error && data.payload !== {}) {
      await setEmployer(data.payload);
    } else {
      await setEmployer(null);
    }
  };

  useEffect(() => {
    getEmployer();
  }, [userdetails]);

  return (
    <>
      <List className="t-color f-size-20">
        {employer &&
          employer.map((item, i) => (
            <AnimatedElement key={item._id}>
              <li className="py-2">
                {item.employerName}
                <br />
                <span className="p-color f-size-15">
                  {new Date(item.startDate).toLocaleDateString("en-US", {
                    month: "long",
                    year: "numeric",
                  })}
                  <span className="d-block d-md-inline px-2">to</span>
                  {new Date(item.endDate).toLocaleDateString("en-US", {
                    month: "long",
                    year: "numeric",
                  })}
                </span>
              </li>
            </AnimatedElement>
          ))}
      </List>
    </>
  );
};

export default PortfolioEmployerSection;
