import React, { useEffect, useState } from "react";
import { Col, List, Row } from "reactstrap";
import AnimatedElement from "./AnimatedElement";
import { useDispatch, useSelector } from "react-redux";
import { getProjectData } from "../reducers/projectReducer";

const PortfolioProjectSection = () => {
  const [projects, setProjects] = useState(null);
  const { userdetails } = useSelector((state) => state.user);
  const dispatch = useDispatch();

  const getProject = async () => {
    const data = await dispatch(getProjectData({ token: userdetails.token }));
    if (!data.error && data.payload !== {}) {
      await setProjects(data.payload);
    } else {
      await setProjects(null);
    }
  };

  useEffect(() => {
    getProject();
  }, [userdetails]);

  return (
    <>
      <List className="t-color f-size-20">
        {projects &&
          projects.map((item, i) => (
            <AnimatedElement key={item._id}>
              <li className="py-2">
                <p className="p-color f-size-20">{item.projectName}</p>
                <Row className="f-size-15">
                  <Col xs={12} md={3}>
                    Responsibility:
                  </Col>
                  <Col xs={12} md={9}>
                    {item.responsibility}
                  </Col>
                </Row>
                <Row className="f-size-15">
                  <Col xs={12} md={3}>
                    Key Technologies:
                  </Col>
                  <Col xs={12} md={9}>
                    {item.technologies}
                  </Col>
                </Row>
              </li>
            </AnimatedElement>
          ))}
      </List>
    </>
  );
};

export default PortfolioProjectSection;
