import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

export const getProjectData = createAsyncThunk(
  "getProjectData",
  async (req, body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      const { data } = await axios.get(
        `${API_URL}/api/project/details/${req.token}`,
        body,
        config
      );
      if (!data.error) {
        return await data;
      } else {
        return "";
      }
    } catch (error) {
      console.log(error);
    }
  }
);

export const updateProjectReducer = createAsyncThunk(
  "updateProject",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/project/update/`,
      body,
      config
    );
    return await data;
  }
);

export const createProjectReducer = createAsyncThunk(
  "createProject",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/project/create/`,
      body,
      config
    );
    return await data;
  }
);

export const deleteProjectReducer = createAsyncThunk(
  "deleteProject",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/project/delete/`,
      body,
      config
    );
    return await data;
  }
);

const initialState = {};

const ProjectReducer = createSlice({
  name: "ProjectReducer",
  initialState,
  reducers: {},
  extraReducers: {
    [getProjectData.fulfilled]: (state, action) => {
      state.ProjectLoading = false;
      if (action.payload.error) {
        state.ProjectError = action.payload.error;
        state.ProjectSuccess = "";
      } else {
        state.Project = action.payload;
        state.success = "";
      }
    },
    [getProjectData.pending]: (state, action) => {
      state.ProjectLoading = true;
      state.Project = null;
      state.ProjectSuccess = "";
      state.ProjectError = "";
    },
    [getProjectData.rejected]: (state, action) => {
      state.ProjectLoading = false;
      if (action.payload.error) {
        state.Project = null;
        state.ProjectError = action.payload.error;
      }
    },
    [updateProjectReducer.fulfilled]: (state, action) => {
      state.ProjectLoading = false;
      if (action.payload.error) {
        state.ProjectError = action.payload.error;
        state.ProjectSuccess = "";
      } else {
        state.Project = action.payload;
        state.ProjectSuccess = "Project data updated successful!!";
      }
    },
    [updateProjectReducer.pending]: (state, action) => {
      state.ProjectLoading = true;
      state.Project = null;
      state.ProjectSuccess = "";
      state.ProjectError = "";
    },
    [updateProjectReducer.rejected]: (state, action) => {
      state.ProjectLoading = false;
      if (action.payload.error) {
        state.Project = null;
        state.ProjectError = action.payload.error;
      }
    },
  },
});

export default ProjectReducer.reducer;
