import React, { useEffect, useState } from "react";
import Portfolio from "../components/PortfolioView/Portfolio";
import Base from "../Base";
import { useSelector } from "react-redux";
import axios from "axios";
const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";
const Portfolios = () => {
  const [userData, setUserData] = useState(null);
  const { userdetails } = useSelector((state) => state.user);

  const getUserInfo = async () => {
    try {
      const { data } = userdetails.token
        ? await axios.get(`${API_URL}/api/user/info/${userdetails.token}`)
        : await axios.get(`${API_URL}/api/user/info/`);
      if (!data.error) {
        await setUserData({ ...data });
      } else {
        await setUserData({});
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getUserInfo();
  }, [userdetails]);
  return <Base>{userData && <Portfolio userData={userData} />}</Base>;
};

export default Portfolios;
