import React from "react";
import { Container, Row, Col } from "reactstrap";
import parse from "html-react-parser";
import AnimatedElement from "../../shared-components/AnimatedElement";
import Skills from "../../shared-components/Skills";
import PortfolioEmployerSection from "../../shared-components/PortfolioEmployerSection";
import PortfolioAcademySection from "../../shared-components/PortfolioAcademySection";
import PortfolioProjectSection from "../../shared-components/PortfolioProjectSection";

const Portfolio = (props) => {
  const userData = props.userData;

  return (
    <>
      <div className="container-fluid s-back py-5">
        <Container>
          <AnimatedElement>
            <div className="career-objective">
              <img src="/images/portfolio.jpg" alt="Portfolio" />
            </div>
            {userData.careerObjective && (
              <>
                <h1 className="p-color f-size-20 mt-5">
                  Career Objectives
                  <hr className="w-200px my-3" />
                </h1>
                <div className="t-color f-size-20">
                  {parse(`${userData.careerObjective}`)}
                </div>
              </>
            )}
          </AnimatedElement>
        </Container>
      </div>
      {userData.careerSummary && (
        <div className="container-fluid py-5">
          <Container>
            <AnimatedElement>
              <h1 className="p-color f-size-20">
                Career Summary
                <hr className="w-200px my-3" />
              </h1>
              <div className="t-color f-size-20">
                {parse(`${userData.careerSummary}`)}
              </div>
            </AnimatedElement>
          </Container>
        </div>
      )}
      {userData.personalQualities && (
        <div className="container-fluid s-back py-5">
          <Container>
            <AnimatedElement>
              <h1 className="p-color f-size-20">
                Personal Qualities
                <hr className="w-200px my-3" />
              </h1>
              <div className="t-color f-size-20">
                {parse(`${userData.personalQualities}`)}
              </div>
            </AnimatedElement>
          </Container>
        </div>
      )}
      <div className="container-fluid py-5">
        <Container>
          <AnimatedElement>
            <h1 className="p-color f-size-20">
              Technical Skills
              <hr className="w-200px my-3" />
            </h1>
            <Skills displayType="column" updateSkill={true} />
          </AnimatedElement>
        </Container>
      </div>
      <div className="container-fluid s-back py-5">
        <Container>
          <AnimatedElement>
            <h1 className="p-color f-size-20">
              Employer :
              <hr className="w-200px my-3" />
            </h1>
          </AnimatedElement>
          <PortfolioEmployerSection />
        </Container>
      </div>
      <div className="container-fluid py-5">
        <Container>
          <AnimatedElement>
            <h1 className="p-color f-size-20">
              Academia :
              <hr className="w-200px my-3" />
            </h1>
          </AnimatedElement>
          <PortfolioAcademySection />
        </Container>
      </div>
      <div className="container-fluid s-back py-5">
        <Container>
          <AnimatedElement>
            <h1 className="p-color f-size-20">
              Projects :
              <hr className="w-200px my-3" />
            </h1>
          </AnimatedElement>
          <PortfolioProjectSection />
        </Container>
      </div>

      <div className="container-fluid py-5">
        <Container>
          <AnimatedElement>
            <h1 className="p-color f-size-20">
              Personal Details
              <hr className="w-200px my-3" />
            </h1>
          </AnimatedElement>
          <AnimatedElement>
            <Row className="f-size-15 t-color">
              {userData.permanentAddress && (
                <>
                  <Col xs={12} md={3}>
                    Permanent Address :
                  </Col>
                  <Col xs={12} md={9} className="no-style-child">
                    {parse(`${userData.permanentAddress}`)}
                  </Col>
                </>
              )}
              {userData.dob && (
                <>
                  <Col xs={12} md={3}>
                    D.O.B :
                  </Col>
                  <Col xs={12} md={9}>
                    {userData.dob}
                  </Col>
                </>
              )}
              {userData.language && (
                <>
                  <Col xs={12} md={3}>
                    Language :
                  </Col>
                  <Col xs={12} md={9}>
                    {userData.language}
                  </Col>
                </>
              )}
              {userData.strength && (
                <>
                  <Col xs={12} md={3}>
                    Strength :
                  </Col>
                  <Col xs={12} md={9}>
                    {userData.strength}
                  </Col>
                </>
              )}
              {userData.mobile && (
                <>
                  <Col xs={12} md={3}>
                    Contact No. :
                  </Col>
                  <Col xs={12} md={9}>
                    {userData.mobile}{" "}
                    {userData.mobile2 ? "/ " + userData.mobile2 : ""}
                  </Col>
                </>
              )}
              {userData.email && (
                <>
                  <Col xs={12} md={3}>
                    Email Id :
                  </Col>
                  <Col xs={12} md={9}>
                    {userData.email}
                  </Col>
                </>
              )}
              {userData.skypeId && (
                <>
                  <Col xs={12} md={3}>
                    Skype Id :
                  </Col>
                  <Col xs={12} md={9}>
                    {userData.skypeId}
                  </Col>
                </>
              )}
            </Row>
          </AnimatedElement>
        </Container>
      </div>
    </>
  );
};

export default Portfolio;
