import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

export const getEmployerData = createAsyncThunk(
  "getEmployerData",
  async (req, body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      const { data } = await axios.get(
        `${API_URL}/api/employer/details/${req.token}`,
        body,
        config
      );
      if (!data.error) {
        return await data;
      } else {
        return "";
      }
    } catch (error) {
      console.log(error);
    }
  }
);

export const updateEmployerReducer = createAsyncThunk(
  "updateEmployer",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/employer/update/`,
      body,
      config
    );
    return await data;
  }
);

export const createEmployerReducer = createAsyncThunk(
  "createEmployer",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/employer/create/`,
      body,
      config
    );
    return await data;
  }
);

export const deleteEmployerReducer = createAsyncThunk(
  "deleteEmployer",
  async (body) => {
    const config = {
      headers: {
        "Content-type": "application/json",
      },
    };
    const { data } = await axios.post(
      `${API_URL}/api/employer/delete/`,
      body,
      config
    );
    return await data;
  }
);

const initialState = {};

const employerReducer = createSlice({
  name: "employerReducer",
  initialState,
  reducers: {},
  extraReducers: {
    [getEmployerData.fulfilled]: (state, action) => {
      state.employerLoading = false;
      if (action.payload.error) {
        state.employerError = action.payload.error;
        state.employerSuccess = "";
      } else {
        state.employer = action.payload;
        state.success = "";
      }
    },
    [getEmployerData.pending]: (state, action) => {
      state.employerLoading = true;
      state.employer = null;
      state.employerSuccess = "";
      state.employerError = "";
    },
    [getEmployerData.rejected]: (state, action) => {
      state.employerLoading = false;
      if (action.payload.error) {
        state.employer = null;
        state.employerError = action.payload.error;
      }
    },
    [updateEmployerReducer.fulfilled]: (state, action) => {
      state.employerLoading = false;
      if (action.payload.error) {
        state.employerError = action.payload.error;
        state.employerSuccess = "";
      } else {
        state.employer = action.payload;
        state.employerSuccess = "Employer data updated successful!!";
      }
    },
    [updateEmployerReducer.pending]: (state, action) => {
      state.employerLoading = true;
      state.employer = null;
      state.employerSuccess = "";
      state.employerError = "";
    },
    [updateEmployerReducer.rejected]: (state, action) => {
      state.employerLoading = false;
      if (action.payload.error) {
        state.employer = null;
        state.employerError = action.payload.error;
      }
    },
  },
});

export default employerReducer.reducer;
